// src/data/sampleListings.ts
const img = (i:number)=>`https://picsum.photos/seed/alt${i}/1200/800`;

type Listing = {
  id: string;
  name: string;
  category: "Venue"|"Decorator"|"Caterer"|"DJ"|"Photographer";
  subtype: string;
  city: string;
  description: string;
  pricePerNight: number;
  pricePerUnit?: number;
  capacity?: number;
  images: string[];
  features: string[];
};

const sample: Listing[] = [];

// helper to push repeats
const pushListing = (i:number, category: Listing["category"], subtype:string, city:string) => {
  sample.push({
    id: `${category.toLowerCase()}-${i}`,
    name: `${subtype} ${category} ${i}`,
    category,
    subtype,
    city,
    description: `A beautiful ${subtype.toLowerCase()} perfect for private events and gatherings. Well-maintained, secure and with great service.`,
    pricePerNight: 15000 + (i*200)%8000,
    capacity: 50 + (i*5)%150,
    images: [img(i*3+1), img(i*3+2), img(i*3+3)],
    features: ["Parking","Power Backup","Indoor","Outdoor","Sound System"].slice(0, 4)
  });
};

// create roughly 30 entries, 6 categories x 5 entries
const subtypesVenue = ["Farmhouse","Villa","Penthouse","Studio","Apartment"];
const subtypesDecorator = ["Floral Decor","Luxury Decor","Minimal Decor","Boho Decor","Theme Decor"];
const subtypesCaterer = ["Indian Cuisine","Italian","Asian Fusion","Continental","Street Food"];
const subtypesDJ = ["House","Hip-Hop","EDM","Bollywood","Mixed"];
const subtypesPhotog = ["Candid","Cinematic","Drone","Fashion","Event"];

let id = 1;
for (let i=0;i<6;i++){
  pushListing(id++, "Venue", subtypesVenue[i%subtypesVenue.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa","Pune"][i%6]);
  pushListing(id++, "Decorator", subtypesDecorator[i%subtypesDecorator.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa","Pune"][i%6]);
  pushListing(id++, "Caterer", subtypesCaterer[i%subtypesCaterer.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa","Pune"][i%6]);
  pushListing(id++, "DJ", subtypesDJ[i%subtypesDJ.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa","Pune"][i%6]);
  pushListing(id++, "Photographer", subtypesPhotog[i%subtypesPhotog.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa","Pune"][i%6]);
}

// additional to reach ~30
for (let j=0;j<5;j++){
  pushListing(id++, "Venue", subtypesVenue[j%subtypesVenue.length], ["Mumbai","Delhi","Bengaluru","Chennai","Goa"][j%5]);
}

export default sample;
