import sampleData from "@/data/sampleListings";

export default function TopRated() {
  const sorted = [...sampleData].sort((a, b) => b.average_rating - a.average_rating);

  return (
    <div className="p-8 text-white">
      <h1 className="text-3xl font-bold mb-4">⭐ Top Rated Venues</h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {sorted.map((venue) => (
          <div key={venue.id} className="bg-[#07102a] p-4 rounded-xl border border-zinc-800">
            <h2 className="text-xl font-semibold">{venue.name}</h2>
            <p className="text-yellow-400">⭐ {venue.average_rating.toFixed(1)}</p>
            <p className="text-zinc-400">{venue.review_count} reviews</p>
          </div>
        ))}
      </div>
    </div>
  );
}
